import request from "@/utils/request";

//
export function getProjectdynamic(query) {
  return request({
    url: "/api/projectdynamic/",
    method: "get",
    params: query
  });
}

//
export function getProjectList(params) {
  return request({
    url: "/api/projects/",
    method: "get",
    params: params
  });
}

//
export function createProject(data) {
  return request({
    url: "/api/projects/",
    method: "post",
    data: data
  });
}

//
export function updataProject(id, data) {
  return request({
    url: "/api/projects/" + id + "/",
    method: "put",
    data: data
  });
}

//
export function delProject(id) {
  return request({
    url: "/api/projects/" + id + "/",
    method: "delete"
  });
}

